import _app from './app.js';

var provinceData = {};
var cityData = {};
var areaData = {};
var streetData = {};
const Sys = uni.getSystemInfoSync();
const wH = Sys.screenHeight;
const wW = Sys.screenWidth;
export default {
	name: 'QS-picker',
	props: {
		pickerId: {
			type: String,
			default: '未定义Id'
		},
		/* show: { //控制是否弹出
			type: Boolean,
			default: false
		}, */
		type: { //类型
			type: String,
			default: 'date'
		},
		height: { //picker高度
			type: Number,
			default: 0
		},
		lineHeight: {
			type: Number,
			default: .09
		},
		indicator_style: { //picker单行样式
			type: String,
			default: ''
		},
		fontscale: { //picker内文字大小
			type: Number,
			default: .02
		},
		width: { //picker宽度
			type: Number,
			default: .8
		},
		buttonSet: { //按钮设置
			type: Object,
			default () {
				return {};
			}
		},
		dataSet: { //各类型携带的数据
			type: Object,
			default () {
				return {};
			}
		},
		showReset: { //每次显示是否重置value
			type: Boolean,
			default: false
		},
		title: { //title标题
			type: String,
			default: ''
		},
		mode: {
			type: String,
			default: 'bottom'
		},
		zIndex: {
			type: [Number, String],
			default: 9999
		},
		bgColor_title: {
			type: String,
			default: '#F8F8F8'
		},
		autoHide: {
			type: Boolean,
			default: true
		},
		titleColor: {
			type: String,
			default: '#999'
		},
		contentColor: {
			type: String,
			default: 'black'
		}
	},
	data() {
		return {
			showPicker: false,
			setObj: {},
			//date
			years: [],
			days: 0,
			value: [],
			//city、provincialStreet
			provinceDataList: [],
			cityDataList: [],
			areaDataList: [],
			//provincialStreet
			streetDataList: [],
			defaultValue: [],
			wH,
			classObj: {
				btnSize: 'font-size: ' + (wH * (this.fontscale + .005)) + 'px;',
			}
		};
	},
	computed: {
		heightSize() {
			if(this.mode==='middle') {
				return wH*(this.height||.3) + 'px';
			}else{
				return wH*(this.height||.45) + 'px';
			}
		},
		titleSize() {
			return (wH * (this.fontscale + .002)) + 'px';
		},
		contentSize() {
			return (wH * this.fontscale) + 'px';
		}
	},
	watch: {
		dataSet() {
			this.init();
		},
		showPicker(n, o) {
			if (n && this.showReset) {
				let defaultValue = this.defaultValue;
				switch (this.type) {
					case 'city':
						this.cityDataList = cityData[defaultValue[0]];
						this.areaDataList = areaData[defaultValue[0]][defaultValue[1]];
						break;
					case 'provincialStreet':
						this.cityDataList = cityData[defaultValue[0]];
						this.areaDataList = areaData[defaultValue[0]][defaultValue[1]];
						this.streetDataList = streetData[defaultValue[0]][defaultValue[1]][defaultValue[2]];
						break;
					case 'date':
						let data = _app.countDays(this.years[defaultValue[0]], defaultValue);
						this.days = data.days;
						this.value = data.val;
						break;
					default:
						break;
				}
				this.value = [...defaultValue];
			}
		}
	},
	created() {
		this.init();
	},
	methods: {
		init() {
			let value = [];
			let setObj = {};
			//date
			let years = [];
			let days = 0;
			// city、provincialStreet
			let provinceDataList;
			let cityDataList;
			let areaDataList;
			//provincialStreet
			let streetDataList;
			
			switch (this.type) {
				case 'city':
					value = this.dataSet.defaultValue || [0, 0, 0];
					provinceData = require('./city-data/province.js').default;
					cityData = require('./city-data/city.js').default;
					areaData = require('./city-data/area.js').default;
					provinceDataList = provinceData;
					cityDataList = cityData[value[0]];
					areaDataList = areaData[value[0]][value[1]];
					//city、provincialStreet
					this.provinceDataList = provinceDataList;
					this.cityDataList = cityDataList;
					this.areaDataList = areaDataList;
					break;
				default:
					break;
			}
			let defaultValue = [...value];
			this.value = value;
			this.defaultValue = defaultValue;
		},
		bindChange({
			detail: {
				value
			}
		}) {
			// console.log(JSON.stringify(e))
			let changevalue;
			switch (this.type) {
				case 'city':
					changevalue = value;
					if (this.value[0] !== changevalue[0]) {
						// 第一级发生滚动
						this.cityDataList = cityData[changevalue[0]];
						this.areaDataList = areaData[changevalue[0]][0];
						changevalue[1] = 0;
						changevalue[2] = 0;
					} else if (this.value[1] !== changevalue[1]) {
						// 第二级滚动
						this.areaDataList = areaData[changevalue[0]][changevalue[1]];
						changevalue[2] = 0;
					}
					this.value = changevalue;
					break;
				default:
					this.value = value;
					break;
			}
		},
		confirm() {
			let type = this.type;
			let value = this.value;
			let obj = {
				type,
				value,
				pickerId: this.pickerId
			}
			let setObj = this.setObj;
			let data;
			switch (type) {
				case 'city':
					data = {
						label: this._getLabel(),
						value: this.value,
						cityCode: this._getCityCode()
					};
					break;
				default:
					break;
			}
			obj.data = data;
			this.$emit('confirm', obj);
			console.log(this.autoHide)
			if(this.autoHide)
				this.hide();
		},
		_getprovincialStreetLabel() {
			let pcikerLabel =
				this.provinceDataList[this.value[0]].label +
				'-' +
				this.cityDataList[this.value[1]].label +
				'-' +
				this.areaDataList[this.value[2]].label +

				(this.streetDataList[this.value[3]] ? '-' + this.streetDataList[this.value[3]] : '');
			return pcikerLabel;
		},
		_getLabel() {
			let pcikerLabel =
				this.provinceDataList[this.value[0]].label +
				'-' +
				this.cityDataList[this.value[1]].label +
				'-' +
				this.areaDataList[this.value[2]].label;
			return pcikerLabel;
		},
		_getCityCode() {
			return this.areaDataList[this.value[2]].value;
		},
		show() {
			this.showPicker = true;
			this.$emit('showQSPicker', {
				pickerId: this.pickerId,
				type: this.type
			});
		},
		hide() {
			this.showPicker = false;
			this.$emit('hideQSPicker', {
				pickerId: this.pickerId,
				type: this.type
			});
		},
		voidFc() {}
	}
}
